import React from 'react'
import './Project.css'
import GIRIN from '../../img/GIRIN.png'
import ForestSeven from '../../img/ForestSeven.PNG'
import Stick from '../../img/dongdongstick.PNG'
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from 'swiper/modules';
import 'swiper/css/pagination'
import 'swiper/css'
import PCard from './PCard'

const Project = () => {

  const clients =[
    {
      img: GIRIN,
      review:"Game Information & Review In Node.js",
      code: "https://github.com/KimIHwan/GIRIN",
      info: "         .\n : Node.js + EJS + MongoDB\n : 2022\n  , , Admin   \n CRUD ."
    },
    {
      img: ForestSeven,
      review:"Unity   ",
      code: "https://github.com/jungang11/Unity_SurvivalGame",
      info: " : C# + Unity\n  : Visual Studio Code + Unity\n  : \nUnity  3D  .    ,      ."
    },
    {
      img: Stick,
      review:"RaspberryPi Python      ",
      info: " : Python\n  : Visual Studio Code + Pycharm + RaspberryPi\n  : \n    , RaspberryPi  .\n       \n    , "
    }
  ]
  return (
    <div className='p-wrapper' id='Project'>
        <div className='p-heading'>
          <span></span>
        </div>
        {/*  */}

        <Swiper
          modules={[Pagination]} // 
          slidesPerView={1} //     
          pagination={{clickable: true}} //  
          loop={true} //    
        >
          {clients.map((client, index) => {
            return(
              <SwiperSlide key={index}>
                <PCard proj={client} />
              </SwiperSlide>
            )
          })}
        </Swiper>
    </div>
  
  )
}

export default Project